<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`BeneficiaryType${dataLayout}`" :key="Date.now()"/>
  </div>
</template>

<script>
import BeneficiaryTypeListLayout from '@/views/beneficiary/beneficiary-type/beneficiary-type-browse/BeneficiaryTypeListLayout'
import BeneficiaryTypeTreeLayout from '@/views/beneficiary/beneficiary-type/beneficiary-type-browse/BeneficiaryTypeTreeLayout'

export default {
  name: 'beneficiary-type-browse',
  components: {
    BeneficiaryTypeListLayout,
    BeneficiaryTypeTreeLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
